import React from 'react'
import ComMenu from '@/components/comMenu/ComMenu.jsx'

import { useTranslation } from "react-i18next";
export default function UserCenter (props) {
  const { t } = useTranslation();
  const menus = [
    {
      title: t('finance.order'),
      to: 'order'
    },
    {
      title: t('finance.transaction'),
      to: 'transaction'
    },
    {
      title: t('finance.balance'),
      to: 'balance'
    },
  ]

  return (
    <>
      <ComMenu menus={menus} ></ComMenu >
    </>
  )
}
